.faculty-list-container { padding-top:4rem; background:linear-gradient(to bottom,#1f2937,#111827); min-height:100vh; padding:2rem 1rem; }
.search-add-container { max-width:1024px; margin:0 auto 2rem; display:flex; flex-wrap:wrap; align-items:center; gap:1rem; position:relative; }
.search-box { position:relative; flex:1; display:flex; }
.search-box input { flex:1; padding:0.75rem 1rem; border:2px solid #d1d5db; border-radius:0.5rem; }
.clear-btn { position:absolute; right:4rem; top:50%; transform:translateY(-50%); background:none; border:none; font-size:1.25rem; color:#6b7280; }
.search-btn { position:absolute; right:0; top:50%; transform:translateY(-50%); background:#7c3aed; color:#fff; padding:0.5rem 1rem; border:none; border-radius:0.5rem; }
.suggestions-dropdown { position:absolute; top:100%; left:0; width:100%; background:#fff; border:1px solid #e5e7eb; border-radius:0.5rem; max-height:16rem; overflow:auto; z-index:10; }
.suggestion-item { display:flex; align-items:center; padding:0.5rem; cursor:pointer; }
.suggestion-img { width:2.5rem; height:2.5rem; border-radius:9999px; margin-right:0.5rem; }
.suggestion-texts p:first-child { font-weight:500; }
.no-suggestions, .suggestion-loading { position:absolute; top:100%; left:0; width:100%; padding:0.5rem; background:#fff; border:1px solid #e5e7eb; border-radius:0.5rem; z-index:10; text-align:center; }
.add-btn { background:linear-gradient(to right,#9333ea,#4f46e5); color:#fff; padding:0.75rem 1.5rem; border-radius:9999px; text-decoration:none; }
.cards-grid { display:grid; grid-template-columns:repeat(auto-fill,minmax(16rem,1fr)); gap:1rem; max-width:1024px; margin:0 auto; }
.loading-spinner { display:flex; justify-content:center; align-items:center; height:12rem; }
.error-state, .empty-state { text-align:center; padding:2rem; color:#d1d5db; }
.error-state button { margin-top:1rem; background:#7c3aed; color:#fff; padding:0.5rem 1rem; border:none; border-radius:0.5rem; }
/* Ensure content sits below navbar */
.faculty-list-container {
  padding-top: 6rem; /* enough to clear the fixed navbar */
  /* existing bg and padding */
}

/* Search input styling */
.search-input {
  flex: 1;
  padding: 0.75rem;
  border: 2px solid #d1d5db;
  border-radius: 0.5rem;
  color: #111827;        /* dark text */
  background: #fff;      /* white background */
}

/* Pagination styling */
.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin: 2rem 0;
}
.pagination button {
  padding: 0.5rem 1rem;
  background: #374151;
  color: #fff;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
}
.pagination button:disabled {
  opacity: 0.5;
  cursor: default;
}

.suggestion-item,
.suggestion-name,
.suggestion-dept,
.no-suggestions,
.suggestion-loading {
  color: #000; /* make suggestions black for readability */
}
.suggestion-item:hover {
  background: #f3f4f6; /* light gray on hover */
}